* {
    padding: 0;
    margin: 0;
    outline: none;
    -webkit-tap-highlight-color: transparent;
}

main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: #f2fdff;
}

.calc {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 1px 5px 9px -2px #00000061;
    border-radius: 5px;
    overflow: hidden;
    background: white;
}

.head {
    font-family: arial;
    color: #18a294;
    width: -webkit-fill-available;
    text-align: center;
    border-bottom: 1px solid #18a294;
    margin-bottom: 1rem;
    padding: 0.5rem 0;
}

.field {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 0.5rem 1.5rem;
}

.field input, .field select{
    border: none;
    border-bottom: 2px solid #18a294;
    margin-top: 10px;
    font-size: 1rem;
    padding: 3px 5px;
    margin-bottom: 1rem;
    font-family: system-ui;
    font-weight: 400;
}

.field label {
    font-family: system-ui;
    font-weight: 500;
    font-size: 1.1rem;
    color: #18a294;
}

.bill:after {
    content: '$';
}

.under_field {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-end;
}

.under_field input {
    margin-left: 1rem;
    margin-right: 1rem;
}

.under_field button {
    width: 41px;
    height: 41px;
    font-size: 2rem;
    border: 2px solid #18a294;
    background: none;
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: #18a294;
    cursor: pointer;
    transition: all 0.3s ease;
}

.under_field button:active {
    background: #18a294;
    color: white;
}

.buttons {
    text-align: center;
}

.buttons button {
    padding: 0.5rem 1rem;
    border: 2px solid #18a294;
    background: none;
    font-size: 1rem;
    font-family: system-ui;
    font-weight: 500;
    color: #18a294;
    margin-bottom: 2rem;
    cursor: pointer;
    transition: all 0.3s ease;
}

.buttons button:active {
    background: #18a294;
    color: white;
}

.result small {
    font-size: inherit;
}

.result {
    font-family: system-ui;
    color: #18a294;
    width: -webkit-fill-available;
    text-align: center;
    border-top: 1px solid #18a294;
    padding: 1rem 0;
}